import { getclassifyList } from '../../api/index.js'
export default{
	data(){
		return{
			classify:[],
			cur:0,
			names:[],
			curName:0,
			items:[]
		}
	},
	onLoad() {
		this.initData()
	},
	onShow() {
		
	},
	methods:{
		initData(){
			getclassifyList().then(res=>{
				this.classify = res.data
				this.names = this.classify[this.cur].children
				this.items = this.names[this.curName].children
			})
		},
		changeClassify(index){
			this.cur = index
			this.names = this.classify[this.cur].children
			this.curName = 0
			this.items = this.names[this.curName].children
		},
		changeName(index){
			this.curName = index
			this.items = this.names[this.curName].children || []
		}
	}
}
